main {
  width: 70vw;
  margin: 40px auto 0px auto;
  // border: 1px solid green;
  box-sizing: content-box;
}

.session-title-mid {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;

  button {
    padding: 15px;
    margin-right: 10px;
    font-family: $pixel;
    border-radius: 10px;
    box-shadow: 2px 2px 5px 2px $gray-5;
    transition: 0.15s;

    &:hover {
      transform: translate(0px, -2px);
      background-color: #faedcb66;
      transition: 0.15s;
    }
  }
}

.main-canvas {
  width: 100%;
  height: 100%;
  // border: 1px solid red;
}

#page-title {
  margin-top: 30px;
}